* {
  text-decoration: none;
  margin: 2px;
  padding: 0;
  list-style: none;
}

.container {
  display: flex;
  height: 100vh;
}
.leftcontainer {
  position: relative;
  flex: 30%;
  width: 250px;
  height: 100%;
  background: #042331;
  padding: 10px;
}
.rightcontainer {
  flex: 70%;
}
header {
  text-align: center;
  color: white;
  font-size: 30px;
  margin-bottom: 25px;
}

.btn {
  display: block;
  width: 100%;
  height: 40px;
  margin-top: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  border-bottom: 1px solid black;
}

.rightcontainer .box {
  position: absolute;
  right: -110%;
  border-radius: 0.5rem;
  box-shadow: var(--box-shadow);
  width: 55rem;
  background: #fff;
}
.rightcontainer .box.active {
  right: -0.5rem;
  transition: 0.1s linear;
}

@media screen and (max-width: 370px) {
  .leftcontainer {
    width: 20px;
    padding: 5px;
  }
  .rightcontainer {
    padding: 1px;
  }

  .rightcontainer .box {
    width: 12rem;
  }
  header {
    text-align: center;
    color: white;
    font-size: 10px;
    margin-bottom: 10px;
  }
  .btn {
    font-size: 5px;
    height: 10px;
    margin-top: 5px;
  }

  .yrs {
    font-size: 12px;
    margin-bottom: 8px;
    color: #042331;
  }
  .qua {
    font-size: 9px;
    text-indent: 10px;
    margin-bottom: 6px;
    color: #0c2835;
  }
  .per {
    font-size: 9px;
    margin-bottom: 25px;
    color: #162a33;
  }
}
